import React, { Component } from 'react';
import { Link } from "react-router-dom";
export default class Header extends Component {
  constructor() {
    super();
    this.state = {
        active: window.location.pathname.substring(1)
    }
    this._handleClick = this._handleClick.bind(this);
  }

  _handleClick(e) {
     this.setState({
        active: e.target.innerHTML
     });
  }

  render() {
    let dashboard = '';
    let projects = '';
    let contact = '';
    if (this.state.active === 'contact') {
      contact = 'current';
    } else if(this.state.active === 'projects') {
      projects = 'current';
    } else {
      dashboard = 'current';
    }
    
    return (
      <React.Fragment>
        <header id="home">
          <nav id="nav-wrap">
              <ul id="nav" className="nav">
                <li className={dashboard} onClick={this._handleClick}><Link to="/">dashboard</Link></li>
                <li className={projects} onClick={this._handleClick}><Link to="/projects">projects</Link></li>
                <li className={contact} onClick={this._handleClick}><Link to="/contact">contact</Link></li>
              </ul>
          </nav>
        </header>
      </React.Fragment>
    );
  }
}