import React, { Component } from 'react';
import Container from 'react-bootstrap/Container'
import 'bootstrap/dist/css/bootstrap.min.css';
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
export default class Projects extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <Container>
        <Row>
          <Col sm={6}>
            <h2 class="text-center">Projects</h2>
            <hr/>
            <div class="project-group">
            {
            resumeData.Projects && resumeData.Projects.map((item)=>{
              return(
                <div>
                  <div class="col-exp">
                    <div class="col-exp-emp">
                      <h4>{item.ProjectName} - {item.ClientName}</h4>
                      <div><b>Role:</b> {item.Position}</div>
                      <div><b>Time Period:</b> {item.PeriodOfTime}</div>
                      <div class="project-desc">{item.Description}</div>
                    </div>
                  </div>
                  <hr/>
                </div>
                )
              })
            }
            </div>
          </Col>
          <Col sm={3}>
            <h2 class="text-center">Achievement</h2>
            <hr/>
            <ul class="achievement">
              {
              resumeData.Achievement && resumeData.Achievement.map((item)=>{
                return(
                  <li><b><span>#</span> {item.project} - </b>{item.appreciation}</li>
                  )
                })
              }
            </ul>
          </Col>
          <Col sm={3}>
            <h2 class="text-center">Education</h2>
            <hr/>
            <div class="achievement">
              {
              resumeData.Education && resumeData.Education.map((item)=>{
                return(
                  <div>
                    <div class="col-exp">
                      <div class="col-exp-emp">
                        <h5>{item.name}</h5>
                        <div><b>Class:</b> {item.Department}</div>
                        <div><b>Time Period:</b> {item.Year}</div>
                        <div><b>Score:</b> {item.Percentage}</div>
                      </div>
                    </div>
                    <hr/>
                  </div>
                  )
                })
              }
            </div>
          </Col>
        </Row>
      </Container>
              
    );
  }
}