import React, { Component } from 'react';
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
export default class ContactUs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isToggled: [...new Array(this.props.resumeData.Projects.length)].map((item) => false)
    };
  }
  _handleClick(i) {
    console.log(i);
    this.setState({
      isToggled: [...new Array(this.props.resumeData.Projects.length)].map((item, idx) =>
      idx === i ? !this.state.isToggled[i] : false)
    });
  }
  render() {
    let resumeData = this.props.resumeData;
    let {isToggled} = this.state;
    return (
      <Container>
        <Row>
          <Col sm={8}>
            <h2 class="text-center">Projects</h2>
            <hr/>
            <div class="projects-group">
            {
            resumeData.Projects && resumeData.Projects.map((item, i)=>{
              return(
                <div>
                  <div class="col-exp">
                    <div class="col-exp-emp">
                      <h4 title="click to expand" onClick={() => this._handleClick(i)}>{item.ProjectName} - {item.ClientName}</h4>
                      {
                        isToggled[i] ?
                          <div class="project-child">
                            <div><b>Client:</b> {item.ClientName}</div>
                            <div><b>Role:</b> {item.Position}</div>
                            <div><b>Time Period:</b> {item.PeriodOfTime}</div>
                            <div><b>Skill Worked:</b> {item.SkillWorked}</div>
                            <div><b>Team Size:</b> {item.TeamSize}</div>
                            <div class="project-desc">{item.Description}</div>
                          </div> : <div></div>
                      }
                    </div>
                  </div>
                  <hr/>
                </div>
                )
              })
            }
            </div>
          </Col>
          <Col sm={4}>
            <h2 class="text-center">Responsibilities in the project</h2>
            <hr/>
            <ul class="project-responsibility">
              {
              resumeData.Responsibilities && resumeData.Responsibilities.map((skill)=>{
                return(
                  <li><span>#</span> {skill.key}</li>
                  )
                })
              }
            </ul>
          </Col>
        </Row>
      </Container>
        );
  }
}